$red: #FFFFE0;

.btn {
    transition-property: all;
    transition-duration: .6s;
    transition-timing-function: ease;
    text-align: center;
    color: #696969;
    cursor: pointer;
    font-size:16px;
    font-weight: 400;
    line-height: 45px;
    margin: 0 0 2em;
    max-width: 160px; 
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    width: 100%; 

  @media(min-width: 600px) { 
    margin: 0 1em 2em; 
  }
}

.btn-1 {
  background: darken($red, 1.5%);
  font-weight: 100;
  transition: 0.3;
  
  svg {
    height: 45px;
    left: 0;
    position: absolute;
    top: 0; 
    width: 100%; 
  }
  
  rect {
    fill: none;
    stroke: #696969;
    stroke-width: 2;
    stroke-dasharray: 422, 0;
    transition: all 0.35s linear;
  }
}

@media (min-width: 770px) {
    .btn-1:hover {
        background: rgba($red, 1);
        font-weight: 900;
        letter-spacing: 1px;
        
        rect {
          stroke-width: 5;
          stroke-dasharray: 15, 310;
          stroke-dashoffset: 48;
          transition: all 1.35s cubic-bezier(0.19, 1, 0.22, 1);
        }
      }
}